.header{height:70px;padding:5px 5px 5px 5px;background-color:#1a4faa;display:-webkit-box;display:flex;-webkit-box-pack:justify;justify-content:space-between;-webkit-box-align:center;align-items:center}.header_logo{display:-webkit-box;display:flex}.header_logo .header_logo--icon{display:-webkit-box;display:flex;-webkit-box-pack:center;justify-content:center;margin:14px 20px}.header_logo .burgerOpen{display:-webkit-box;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-direction:column;justify-content:space-around;width:2em;height:2em;background:transparent;border:none;cursor:pointer;padding:0;z-index:10}.header_logo .burgerOpen:focus{outline:none}.header_logo .lineBurger{width:2rem;height:0.25rem;background:white;border-radius:10px;-webkit-transition:all 0.3s linear;transition:all 0.3s linear;position:relative;-webkit-transform-origin:1px;transform-origin:1px}.header_logo .burgerClosed{display:-webkit-box;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-direction:column;justify-content:space-around;width:2em;height:2em;background:transparent;border:none;cursor:pointer;padding:0;z-index:10}.header_logo .burgerClosed:focus{outline:none}.header_logo .burgerClosed :first-child{-webkit-transform:rotate(45deg);transform:rotate(45deg)}.header_logo .burgerClosed :nth-child(2){opacity:0}.header_logo .burgerClosed :nth-child(3){-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}.header_logo .header_logo--image{margin-left:23px;width:120px}.header_links{color:white;padding-right:25px;display:-webkit-box;display:flex}a{color:white;cursor:pointer;font-size:14px;padding-left:30px;text-decoration:none}a:hover{text-decoration:underline}
