.flex{display:-webkit-box;display:flex;-webkit-box-align:center;align-items:center}#login_content{height:95%}.login{font-family:'Nunito', sans-serif;background:-webkit-gradient(linear, left top, left bottom, from(#1a4faa), to(#112f91));background:linear-gradient(#1a4faa, #112f91);display:-webkit-box;display:flex;-webkit-box-align:center;align-items:center;-webkit-box-pack:center;justify-content:center;-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-direction:column;padding:0px 30px;height:100%}.login h2{text-align:center;font-size:35px}.login__container{background-color:rgba(255,255,255,0.1);border:2px solid white;border-radius:40px;color:white;padding:60px 68px 40px;height:70%;width:20%;display:-webkit-box;display:flex;justify-content:space-around;-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-direction:column}.login__container--form{display:-webkit-box;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-direction:column}.login__container--form label{font-size:14px}.login__container--remember-me{color:white;display:-webkit-box;display:flex;-webkit-box-pack:justify;justify-content:space-between;margin-top:10px}.login__container--remember-me a{color:white;font-size:14px;text-decoration:none}.login__container--remember-me a:hover{text-decoration:underline}.login__container--socialmedia>div{display:-webkit-box;display:flex;-webkit-box-align:center;align-items:center;font-size:14px;margin-bottom:10px}.login__container--socialmedia>div a{text-decoration:none;color:white;font-weight:bold}.login__container--socialmedia img{width:30px;margin-right:10px}.login__container--register{font-size:14px}.login__container--register a{color:white;font-weight:bold;text-decoration:none}.login__container--register a:hover{text-decoration:underline}.input{background-color:transparent;color:white;border-left:0px;border-top:0px;border-right:0px;border-bottom:2px solid white;font-family:"Muli", sans-serif;font-size:16px;margin-bottom:20px;padding:0px 20px;outline:none;height:50px}.input:focus{border-bottom:2px solid #76bdff;-webkit-transition:0.4s;transition:0.4s;left:0}::-webkit-input-placeholder{color:white}::-moz-placeholder{color:white}:-ms-input-placeholder{color:white}::-ms-input-placeholder{color:white}::placeholder{color:white}@media only screen and (max-width: 600px){.login__container{padding:0;width:100% !important}}@media only screen and (max-width: 1500px){.login__container{padding:60px 68px 40px;width:17% !important;display:-webkit-box;display:flex;justify-content:space-around;-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-direction:column}}@media only screen and (max-width: 1000px){.login__container{padding:60px 68px 40px;width:20% !important;display:-webkit-box;display:flex;justify-content:space-around;-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-direction:column}}
